
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {sendSMSCode, checkCode} from '@/api/user.js'
import { getToken } from '@/utils/auth'
export default {
name: 'backPassword',
components: {
XInput,
Group,
XButton
},
data () {
return {
getCodeStatus: false,
countdown: 60,
postData: {
mob: '',
code: '',
piccode:''
},
piccode:''
}
},
created () {
this.getPicCode();
},
methods: {
getPicCode(){
this.piccode = process.env.BASE_API+'/Api/System/getPicCode&token=' + getToken() + ('&random=' + Math.random())
},

sendSMSCode () {
if (this.getCodeStatus) {

return
}




const reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/
if (!reg.test(this.postData.mob)) {
if (this.postData.mob === '' || this.postData.mob === null) {
this.$vux.toast.text('请填写手机号', 'middle')
return
}
this.$vux.toast.text('手机号有误')
return
}
if (this.postData.piccode === '' || this.postData.piccode === null) {
this.$vux.toast.text('请填写图形验证码', 'middle')
return
}
const postData = {
mob: this.postData.mob,
piccode:this.postData.piccode,
type: 1 
}
const _this = this
sendSMSCode(postData)
.then(res => {
if (res.data.errorcode === 0) {

_this.$vux.toast.text('已发送,请注意查收！', 'middle')
_this.getCodeStatus = true
let interVal = setInterval(function () {
if (_this.getCodeStatus && _this.countdown > 0) {
_this.countdown--
} else {
_this.getCodeStatus = false
clearInterval(interVal)
_this.countdown = 60
}
}, 1000)
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

next () {
if (this.postData.mob === '' || this.postData.code === '') {
this.$vux.toast.text('请填写完整信息', 'middle')
return
}
checkCode(this.postData)
.then(res => {
if (res.data.errorcode === 0) {

console.log(res.data.errorcode)

this.$router.push({path: '/editPassword'})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.vux.toast.text('网络异常！', 'middle')
})
}
}
}



